@use "../abstracts/variables";
@use "../abstracts/mixins";

$break-slider: "max-width: 768px";
$break-slider-m: "max-width: 991px";

.carousel-collections {
  padding: 25px 0;
  background-color: variables.$color-secondary;

  &__wrapper {
    max-width: 940px;
    margin: 0 auto;

    @include mixins.mediaCustom("max-width: 992px") {
      padding-left: variables.$paddings-side--movil;
      padding-right: variables.$paddings-side--movil;
      overflow-x: hidden;
    }

    @include mixins.mediaCustom($break-slider) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__slides {
    .swiper-wrapper {
      @include mixins.mediaCustom($break-slider) {
        justify-content: flex-start;
        @include mixins.marginsInside;
        // Overwrite
        margin: 0;

      }
    }

    &--flex {
      @include mixins.mediaCustom($break-slider) {
        @include mixins.boxFlex(center, initial);
      }

      .swiper-wrapper {
        @include mixins.boxFlex(center, flex-start);

        @include mixins.mediaCustom($break-slider) {
          width: auto;
          justify-content: flex-start;
          padding: 0 variables.$paddings-side--movil;
        }

        & > * {
          width: 100px;
          max-width: 100px;

          @include mixins.mediaCustom($break-slider) {
            max-width: 85px;
          }

          &:not(:last-child) {
            margin-right: 40px;

            @include mixins.mediaCustom('max-width: 1100px') {
              margin-right: 28px;
            }
          }

          @include mixins.mediaCustom($break-slider) {
            width: auto;
          }
        }
      }

      .wrapper__slider button.swiper-button {
        display: none;
      }
    }

    &--slider {
      .swiper-wrapper {
        margin-left: auto;
        margin-right: auto;
        width: 100%;
      }

      .swiper:not(.swiper-initialized) {
        .swiper-wrapper > * {
          max-width: 100px;
          margin-right: 40px;

          @include mixins.mediaCustom($break-slider) {
            margin-right: 28px;
            max-width: 85px;
          }
        }
      }

      .wrapper__slider button.swiper-button {
        align-items: center;
        height: 40px;
        width: 40px;
      }

      .wrapper__slider button.swiper-button-prev {
        left: -77px;
      }

      .wrapper__slider button.swiper-button-next {
        right: -77px;
      }
    }
  }

  &__slide {
    max-width: 100px;
    text-align: center;

    @include mixins.mediaCustom($break-slider) {
      max-width: 85px;
    }

    &:not(:last-child) {
      @include mixins.mediaCustom($break-slider) {
        margin-right: 19px;
      }
    }

    a {
      display: block;
      line-height: 0;
    }

    img {
      height: auto;
      margin-bottom: 14px;
      width: 100%;
      border-radius: 100%;

      @include mixins.mediaCustom($break-slider) {
        margin-bottom: 8px;
      }
    }

    span {
      display: block;
      font-size: variables.$font-size--general-up-s;
      line-height: 18px;
      text-align: center;
      text-transform: uppercase;
      color: variables.$color-principal;

      @include mixins.media("tablet-l") {
        font-size: variables.$font-size--general-up-m;
        line-height: 17px;
      }
    }
  }
}
