@use "../theme/theme";
@use "../abstracts/variables";
@use "../abstracts/mixins";

.collection-hero {
  position: relative;
}

.collection-hero__img {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;

  @include mixins.media("tablet-l") {
    z-index: -1;
  }
}

.collection-hero__content-wrapper {
  position: relative;
  width: 90%;
  padding: 30px 0;
  margin: 0 auto;

  @include mixins.media("tablet") {
    width: 561px;
  }
}

.collection-hero__content {
  padding: 12px;
  text-align: center;

  @include mixins.media("tablet-l") {
    padding: 20px;
  }
}

.collection-hero__title.h1 {
  text-align: center;
  text-transform: uppercase;
  font-size: variables.$font-size--mobile--h1-customer;
  line-height: variables.$line-height--mobile--h1-customer;

  @include mixins.media("tablet-l") {
    line-height: 40px;
    font-size: variables.$font-size--mobile-h1;
  }
}

.collection-hero__description {
  line-height: 1.5em;
}
