@use '../abstracts/variables';

.filter-group-display {
  &__list {
    padding-left: 1px;
    list-style: none;

    li:not(li:last-of-type) {
      padding-bottom: .5rem;
    }

    li {
      margin-bottom: 15px;
    }

    &-item {
      label {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
      }

      input {
        margin-right: .5rem;
      }
    }

    &.color {
      display: flex;
      flex-direction: row;
      /* stylelint-disable-next-line declaration-block-no-redundant-longhand-properties */
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-start;

      li:not(li:last-of-type) {
        padding-right: calc(.5rem + 4px );
      }

      input[type="checkbox"] {
        display: none;

        & + span {
          display: block;
          display: flex;
          width: 32px;
          height: 32px;
          box-sizing: content-box;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          border: 1px solid transparent;
          border-radius: 100%;
          cursor: pointer;
          font-size: 0;
          transition: all .3s;

          & > span {
            width: 24px;
            height: 24px;
            border-radius: 100%;
          }
        }

        &[value="White"] + span > span {
          border: 1px solid variables.$color-black;
        }

        &:checked + span {
          border: 1px solid variables.$color-black;
        }
      }
    }
  }
}
