@use '../abstracts/mixins';

.infinity__scroll {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px 0;

  &--loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 55px;

    @include mixins.mediaCustom('min-width: 767px') {
      margin-top: 80px;
    }
  }
}
