@use "../abstracts/variables";
@use "../abstracts/mixins";

.filter {
  @include mixins.mediaCustom("max-width: 767px") {
    position: fixed;
    z-index: 12;
    top: 0;
    right: -100%;
    display: flex;
    height: 100%;
    padding: 0;
    width: 100%;
    background: #fff;
    box-shadow: 1px 3px 15px rgba(0, 0, 0, 0.131);
    transition: right 0.3s;
  }

  @include mixins.media("tablet") {
    overflow-y: auto;
    max-height: max(57vh);
  }

  @include mixins.media("desktop-m") {
    max-height: max(62vh);
  }

  @include mixins.media("desktop-l") {
    max-height: max(66vh);
  }

  @include mixins.media("desktop-xl") {
    max-height: max(68vh);
  }

  &::-webkit-scrollbar {
    @include mixins.media("tablet") {
      display: none;
    }
  }


  @include mixins.mediaCustom("min-width: 768px") {
    &::-webkit-scrollbar {
      display: none;
    }
  }

  &[data-active="true"] {
    @include mixins.mediaCustom("max-width: 767px") {
      right: 0;
    }
  }

  &[data-active="false"] {
    @include mixins.mediaCustom("min-width: 768px") {
      right: 0;
    }
  }

  .accordions {
    display: block;
    grid-row-gap: 0px;
    row-gap: 0px;

    @include mixins.mediaCustom("max-width: 767px") {
      display: flex;
      flex-direction: column;
      height: 100%;
      overflow-y: auto;
      @include mixins.scroll(variables.$color-background, 5px, 5px, variables.$color-background-5);
    }


    .accordion-item.custom-filters {
      .accordion-item__content {
        @include mixins.boxFlex(initial, initial, column);
      }

      li {
        padding-bottom: 0;
      }

      a {
        font-size: variables.$font-size--general-up-s;
        color: variables.$color-background-2;
      }
    }

    .accordion-item.has-search .accordion-item__button[data-active=true] ~ div.accordion-item__content {
      @include mixins.mediaCustom("min-width: 768px") {
        max-height: 376px;
      }
    }

    .accordion-item:not(:last-of-type)  {
      @include mixins.mediaCustom("max-width: 767px") {
        padding: 12px 27px;
        border-bottom: 1px solid variables.$color-background;

      }

      .accordion-item__button {
        @include mixins.mediaCustom("min-width: 768px") {
          border-bottom: 1px solid variables.$color-background;
          padding: 12px 0;
        }
      }
    }

    .accordion-item__button {
      border: none;
      padding: 0;
      background-color: unset;
      position: relative;
      z-index: 2;
      color: variables.$color-black;

      svg {
        width: 11px;
      }

      span {
        font-size: variables.$font-size--general-up-m;
        line-height: variables.$line-height--general-m;
        text-transform: uppercase;
        color: variables.$color-black;
      }
    }

    .accordion-item__button[data-active="true"] {
      padding: 0 0 16px 0;

      @include mixins.mediaCustom("max-width: 767px") {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 5;
        border-bottom: 1px solid variables.$color-background;
        flex-direction: row-reverse;
        justify-content: flex-end;
        padding: 12px 16px 13.5px;
        width: 100%;
        background-color: variables.$color-white;
      }

      @include mixins.mediaCustom("min-width: 768px") {
        margin-bottom: 12px;
      }

      svg {
        @include mixins.mediaCustom("max-width: 767px") {
          margin-right: 17px;
          transform: rotate(90deg);
          width: 13px;
          margin-left: 0;
        }
      }
    }

    .accordion-item__button[data-active="true"] ~ div.accordion-item__content {
      padding: 0;
      overflow-y: auto;
      overflow-x: hidden;
      max-height: 320px;

      @include mixins.scroll(variables.$color-background-3, 5px, 5px, variables.$color-background-5);

      @include mixins.mediaCustom("max-width: 767px") {
        position: absolute;
        left: 0;
        top: 51px;
        z-index: 5;
        height: 100%;
        padding: 0;
        padding-bottom: 50px;
        width: 100%;
        background: variables.$color-white;
        max-height: calc(100% - 80px);
      }
    }

    .accordion-item__content {
      background-color: unset;

      @include mixins.mediaCustom("max-width: 767px") {
        @include mixins.boxFlex(initial, initial, column);
      }
    }

    .filter__input-searching {
      border-bottom: 1px solid variables.$color-background;

      @include mixins.mediaCustom("min-width: 768px") {
        border-bottom: none;
        margin-bottom: 12px;
        width: 95%;
      }


      input {
        padding-left: 42px;

        @include mixins.mediaCustom("max-width: 767px") {
          border: none;
          padding-left: 45px;
        }
      }

      svg {
        left: 10px;

        @include mixins.mediaCustom("max-width: 767px") {
          left: 13px;
        }
      }
    }
    .range {
      margin-bottom: 12px;
      @include mixins.boxFlex(space-between);

      &-element {
        padding: 5px;
        border: 1px solid;
        border-radius: 5px;
      }

      input[type="number"] {
        border-style: none;
      }

      & > label {
        margin: 0 10px;
      }
    }

    .filter-group-display__list {
      padding: 0;
      list-style: none;
    }

    .filter-group-display__list-item {
      margin-bottom: 12px;
      @include mixins.boxFlex(space-between, initial, column);

      @include mixins.mediaCustom("max-width: 767px") {
        border-bottom: 1px solid variables.$color-background;
        margin: 0;
        padding: 12px 20px;
      }

      &.sub-item {
        padding: 0 0 8px 20px;

        @include mixins.mediaCustom("max-width: 767px") {
          margin-left: -20px;
          margin-right: -20px;
          padding: 12px 40px;
        }
      }

      &.sub-item:first-of-type {
        margin-top: 12px;
        padding-top: 8px;

        @include mixins.mediaCustom("max-width: 767px") {
          border-top: 1px solid variables.$color-background;
          padding-top: 12px;
        }
      }

      &.sub-item:last-child {
        margin-bottom: 0;
        padding-bottom: 0;
        padding-top: 0;

        @include mixins.mediaCustom("max-width: 767px") {
          border-bottom: unset;
          padding-top: 12px;
        }
      }

      span {
        color: variables.$color-text-2;
        cursor: pointer;
        font-size: 14px;
        word-break: break-all;
        padding-right: 5px;
      }

      .slider {
        border-color: variables.$color-text-2;
        margin-right: 20px;

        &.color-image {
          margin-right: 8px;

          @include mixins.mediaCustom("min-width: 768px") {
            display: none;
          }
        }
      }

      .slider + span {
        width: calc(100% - 16px);
      }

      .form__checkbox input:checked ~ * {
        font-weight: variables.$font-medium;
      }
    }

    .filter-group-display__list[data-type-filter="filter.p.m.custom.volume"] {
      column-gap: 16px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      padding: 12px 27px;
      row-gap: 12px;

      @include mixins.mediaCustom("min-width: 768px") {
        column-gap: 8px;
        padding: 0;
        row-gap: 0;
      }

      img {
        height: 64px;
        width: 64px;
      }

      .slider {
        display: none;
      }

      .form__checkbox {
        flex-direction: column;
        padding: 8px;
        border: 1px solid variables.$color-background;
        height: 100%;
        width: 88px;
        text-align: center;
        @include mixins.clickEffect;

        @include mixins.mediaCustom("min-width: 768px") {
          width: auto;
        }
      }

      .form__checkbox:has(input:checked) {
        border: 1px solid variables.$color-principal;
      }

      .form__checkbox:has(input:checked) span {
        color: variables.$color-principal;
      }

      .form__checkbox span {
        margin-top: 8px;
        font-weight: variables.$font-light;
        font-size: 11px;
      }

      .form__checkbox span > span {
        display: none;
      }

      li {
        align-items: center;
        border-bottom: unset;
        padding: 0;
      }
    }

    .filter-group-display__list[data-type-filter="filter.p.m.custom.hardware"],
    .filter-group-display__list[data-type-filter="filter.p.m.custom.extern_colors_filtres_"] {
      @include mixins.mediaCustom("min-width: 768px") {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-items: center;
      }

      img {
        height: 48px;
        width: 48px;
        border-radius: 100%;

        @include mixins.mediaCustom("max-width: 767px") {
          height: 16px;
          width: 16px;
          margin-right: 12px;
        }
      }

      .form__checkbox {
        @include mixins.mediaCustom("min-width: 768px") {
          position: relative;
          flex-direction: column;
          @include mixins.clickEffect;
        }
      }

      .form__checkbox input:checked ~ img {
        border: 2px solid variables.$color-principal;
      }

      .form__checkbox span {
        @include mixins.mediaCustom("min-width: 768px") {
          position: absolute;
          bottom: -20px;
          opacity: 0;
          transition: opacity .3s;
          font-size: variables.$font-size--general-s;
          white-space: nowrap;
          padding: 0;
          word-break: unset;
        }
      }

      .form__checkbox:hover span {
        @include mixins.mediaCustom("min-width: 768px") {
          opacity: 1;
        }
      }

      .form__checkbox span > span {
        @include mixins.mediaCustom("min-width: 768px") {
          display: none;
        }
      }

      li {
        @include mixins.mediaCustom("min-width: 768px") {
          height: 68px;
          padding-bottom: 0.5rem;
          width: 48px;
        }
      }
    }
  }
}

.filter-form {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  @include mixins.boxFlex(initial, initial, column);

  &::-webkit-scrollbar {
    display: none;
  }

  &__header-controls {
    padding: 12px 27px;
    border-bottom: 1px solid variables.$color-background;
    @include mixins.boxFlex(space-between);

    @include mixins.mediaCustom("min-width: 768px") {
      display: none;
    }

    button {
      cursor: pointer;
      font-size: variables.$font-size--general-m-major;
      font-weight: variables.$font-regular;
      @include mixins.boxFlex();
    }

    svg {
      height: 13px;
      margin-right: 16px;
      width: 13px;
      transform: rotate(90deg);
    }

    .filter-form__submit-remove {
      margin: 0;
    }
  }

  &__submit {
    margin-top: auto;
    position: sticky;
    position: -webkit-sticky;
    bottom: 0;
    padding: 15px 0;
    @include mixins.boxFlex(space-between);

    @include mixins.mediaCustom("min-width: 768px") {
      margin-top: 0;
      display: block;
    }
  }

  &__submit.is-mobile {
    bottom: 0;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    padding: 0 27px 27px;
    width: 100%;
    background-color: variables.$color-white;

    @include mixins.mediaCustom("min-width: 375px") {
      justify-content: space-between;
    }

    @include mixins.mediaCustom("min-width: 768px") {
      display: none;
    }
    .btn-1 {
      width: 100%;
    }
  }
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #ff4500;
  cursor: ew-resize;
  box-shadow: 0 0 2px 0 #555;
  transition: background 0.3s ease-in-out;
}
