@use "../abstracts/variables";
@use "../abstracts/mixins";

.article-banner {
  grid-column-end: span 2;
  position: relative;
  @include mixins.boxFlex(initial, center, column);

  @include mixins.media("tablet-l") {
    flex-direction: row;
  }

  @include mixins.media('tablet-l') {
    grid-column-end: span 3;
  }

  &.img-position--D {
    @include mixins.mediaCustom("max-width: 1199px") {
      flex-direction: column-reverse;
    }
  }

  &.img-position--R {
    @include mixins.media("tablet-l") {
      flex-direction: row-reverse;
    }
  }
}

.article-banner__img-container {
  position: relative;
  display: block;
  height: 100%;
  padding-bottom: 46.52%;
  width: 100%;
  line-height: 0;

  @include mixins.media("tablet-l") {
    padding-bottom: 30.3%;
    width: 50%;
  }

  img {
    bottom: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;

    @include mixins.media("tablet-l") {
      margin-bottom: 0;
      max-width: initial;
    }
  }
}

.banner-promo__content {
  padding: 24px;
  text-align: center;
  width: 100%;
  @include mixins.boxFlex(center, center, column);

  @include mixins.media("tablet-l") {
    padding: variables.$paddings-side--movil;
    width: 50%;
  }

  .banner-promo__title {
    font-family: variables.$font-family--title;
    font-weight: 300;
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
  }

  .h2 {
    margin-bottom: 8px;
  }

  .banner-promo__text {
    margin-bottom: 16px;
    font-size: variables.$font-size--general-m;
    line-height: 20px;

    @include mixins.media('tablet-l')  {
      margin-bottom: 28px;
      width: 70%;
    }

  }
}
