.overlay {
  position: fixed;
  z-index: 4;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);

  &.overlay--loader-filters {
    z-index: 16;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;

    .loading {
      width: 50px;
      height: 50px;
      border-width: 4px;
    }
  }
}
