@use "../abstracts/variables";
@use "../abstracts/mixins";

.collection {
  margin-top: 22px;
  width: 100%;

  @include mixins.mediaCustom("min-width: 768px") {
    @include mixins.boxGrid(min(280px) 1fr, unset, 40px, 0);
  }

  @include mixins.mediaCustom("min-width: 1366px") {
    @include mixins.boxGrid(min(300px) 1fr, unset, 40px, 0);
  }

  @include mixins.media("tablet-l") {
    column-gap: 66px;
  }
}

.collection__container.wrapper-without-limit {
  width: 100%;

  @include mixins.mediaCustom("max-width: 991px") {
    margin-left: auto;
    margin-right: auto;
    padding-right: variables.$paddings-side--movil;
    padding-left: variables.$paddings-side--movil;
  }
}

.collection__products-grid {
  .item-list {
    @include mixins.boxGrid(1fr, unset, 16px, 20px);

    @include mixins.mediaCustom("min-width: 360px") {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(repeat(3, 1fr), unset, 20px, 32px);
    }
  }

  .product-card--head-images:hover .product-card__image-2 {
    @include mixins.mediaCustom("max-width: 991px") {
      opacity: 0;
      z-index: 0;
    }
  }

  .product-card .wk-button.wk-button--add.wk-button--floating {
    height: 25px;
    width: 26px;

    @include mixins.media("tablet-l") {
      height: 31px;
      width: 34px;
    }
  }
}

.collection__products-count-wrapper {
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  margin-right: -16px;
  margin-left: -16px;
  @include mixins.boxGrid(1fr 1fr, initial, 0);

  @include mixins.media("tablet") {
    margin-right: 0;
    margin-left: 0;
    @include mixins.boxGrid(230px 230px, initial, 30px);
  }

  @include mixins.mediaCustom("min-width: 768px") {
    @include mixins.boxFlex(space-between, flex-start);
  }

  button:not(#js-btn-mes-alerts-submit, .mes-alerts__open-btn) {
    padding: 10px 7px;
    border-right: none;
    border-color: variables.$color-principal;
    color: variables.$color-principal;

    @include mixins.mediaCustom("min-width: 360px") {
      padding: 10px;
    }

    @include mixins.mediaCustom("min-width: 768px") {
      display: none;

      border-color: variables.$color-black;
      color: variables.$color-black;
    }

    &:active {
      border-right: 1px solid variables.$color-principal;
    }

    svg {
      margin-right: 6px;
    }

    svg path, svg mask {
      fill: variables.$color-principal;

      @include mixins.mediaCustom("min-width: 768px") {
        fill: variables.$color-black;
      }
    }
  }

  .collection-filters__sort-by {
    margin-left: auto;
  }

  .collection-filters__sort-by select {
    @include mixins.mediaCustom("max-width: 767px") {
      border-color: variables.$color-principal;
      color: variables.$color-principal;
      background-image: var(--icon-caret-blue);
    }
  }

  .mes-alerts {
    padding-top: 22px;

    @include mixins.mediaCustom("min-width: 768px") {
      padding-top: 16px;
    }

    .mes-alerts__open-btn span {
      @include mixins.mediaCustom("max-width: 767px") {
        margin: 0;
      }
    }
  }
}

.collection__products-count {
  display: block;
  color: variables.$color-principal;

  @include mixins.mediaCustom("min-width: 768px") {
  }
}
