@use '../theme/theme';
@use '../abstracts/variables';
@use '../abstracts/mixins';

.text-seo {
  display: flex;
  order: 1;
}

.text-seo__bg {
  padding-bottom: 55px;
  padding-top: 45px;
  width: 100%;
  background-color: variables.$color-secondary;

  @include mixins.media('tablet-l') {
    padding-bottom: 60px;
    padding-top: 50px;
  }
}

.text-seo__container {
  width: 100%;
  text-align: center;

  @include mixins.media('tablet-l') {
    @include mixins.mediaCustom('max-width: 1250px') {
      padding-left: variables.$paddings-side-m;
      padding-right: variables.$paddings-side-m;
    }
  }

  .text-seo__content {
    padding: 12px;
    width: 100%;
  }

  .text-seo__content--hidden {
    height: 0;
    padding: 0;
    overflow-y: hidden;
    transition: all .5s;

    p {
      margin-bottom: 12px;
    }

    p:last-of-type {
      margin-bottom: 0;
    }
  }

  .text-seo__content--hidden[data-active="true"] {
    height: auto;
    padding-top: 14px;
    padding-bottom: 18px;

    & ~ button {
      margin: 0 auto;
    }

    & ~ button .less {
      display: block;
    }

    & ~ button .plus {
      display: none;
    }
  }

  button {
    margin: 18px auto;
    color: variables.$color-principal;
    @include mixins.linkEffect(currentColor, 1px);

    .less {
      display: none;
    }
  }

  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 12px;
    font-size: variables.$font-size--general-m;
    font-weight: 400;
    line-height: variables.$line-height--general-m;
  }

  h1 {
    margin-bottom: 8px;
    color: variables.$color-principal;
    text-transform: uppercase;
    font-weight: 400;
    font-size: variables.$font-size--mobile-h2;
    line-height: 30px;
  }

  h1, h2 {
    text-transform: uppercase;
  }

  h2, h3 {
    font-size: variables.$font-size--general-up-l;
    line-height: 1.4em;
  }

  a {
    color: variables.$color-principal;
    @include mixins.linkEffect(currentColor, 1px);

    &:after {
      bottom: 4px;
    }
  }

  div {
    line-height: variables.$line-height--general-m;
  }
}
