@use "../abstracts/variables";
@use "../abstracts/mixins";

.collection__filters-wrapper {
  position: relative;
}

.collection__filters-bottom-btns {
  display: none;
  width: 100%;
  margin-bottom: 20px;

  @include mixins.media("tablet") {
    display: block;
  }
}

.collection__filters {
  top: 110px;
  left: 0;
  height: fit-content;
  z-index: 6;

  @include mixins.media("tablet") {
    position: sticky;
    margin-bottom: 40px;
    z-index: 3;
  }

  & > .collection-filters__remove {
    margin-top: 10px;

    @include mixins.mediaCustom("max-width: 767px") {
      display: none;
    }
  }
}

.collection-filters {
  &__actives {
    display: none;

    @include mixins.mediaCustom("min-width: 768px") {
      display: flex;
      flex-wrap: wrap;
    }

    &.is-mobile {
      border-bottom: 1px solid variables.$color-background;
      display: flex;
      padding: 12px 27px;
      background-color: variables.$color-secondary;
      overflow-x: auto;
      @include mixins.scroll(variables.$color-background-3, 5px, 5px, variables.$color-background-5);

      @include mixins.mediaCustom("min-width: 768px") {
        display: none;
      }
    }

    .active-filters__tag {
      border: 1px solid variables.$color-principal;
      display: flex;
      margin-bottom: 8px;
      margin-right: 16px;
      padding: 4px 8px;
      font-size: variables.$font-size--general-m;
      font-weight: variables.$font-regular;
      line-height: variables.$line-height--general-m;
      text-transform: uppercase;
      color: variables.$color-principal;
      background-color: variables.$color-white;
      white-space: nowrap;

      @include mixins.media("tablet-l") {
        align-items: center;
        justify-content: center;
        margin-bottom: 12px;
        min-height: 43px;
      }
    }

    .active-filters__remove-filter {
      margin-left: 6px;
    }

    .active-filters__remove-filter:hover,
    .active-filters__remove-filter:visited,
    .active-filters__remove-filter:active {
      color: variables.$color-principal;
    }
  }

  &__controls {
    width: 100%;
  }

  &__controls .collection-filters__sort-by {
    @include mixins.mediaCustom("max-width: 767px") {
      display: none;
    }
  }

  .mes-alerts__open-btn {
    width: 100%;

    @include mixins.mediaCustom("max-width: 767px") {
      width: auto;
      margin: 0 auto;
    }
  }

  .filter-option {
    display: flex;
    align-items: center;

    label {
      position: relative;
      top: auto;
      right: auto;
      bottom: auto;
      left: auto;
      display: flex;
      overflow: hidden;
      height: 100%;
      box-sizing: border-box;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      padding: 8px 16px;
      margin-left: 16px;
      background-color: #b1b1b1;
      border-radius: 118px;
      color: variables.$color-white;
      cursor: pointer;
      text-transform: uppercase;

      &:last-of-type {
        margin-right: 0;
      }

      .filter-design {
        position: absolute;
        top: 0;
        right: auto;
        bottom: auto;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #009ae2;
        opacity: 0;
        transition: opacity 0.5s ease;
      }

      .filter-label {
        position: relative;
      }

      input:checked ~ .filter-design {
        opacity: 1;
      }

      input:checked ~ .filter-label {
        color: variables.$color-white;
      }
    }
  }

  .collection-filters__remove {
    margin-bottom: 26px;

    @include mixins.mediaCustom("max-width: 767px") {
      display: none;
    }
  }
}

.collection-filters__sort-by {
  height: 100%;
  width: 100%;

  @include mixins.media("tablet") {
    width: 170px;
  }

  .form__textfield {
    height: 100%;
  }

  select {
    margin-bottom: 0;
    padding: 10px 7px;
    padding-right: 27px;
    height: 100%;
    width: 100%;
    text-transform: uppercase;
    border-color: variables.$color-black;
    color: variables.$color-black;
    background-image: var(--icon-caret);
    background-size: 14px;
    background-position: calc(100% - 10px);
    text-align-last: center;

    @include mixins.mediaCustom("min-width: 360px") {
      padding: 10px 16px 10px;
      padding-right: 34px;
      background-position: calc(100% - 16px);
    }
  }
}

.btn-1.collection-filters__scrollup {
  margin-top: 12px;
  width: 100%;

  @include mixins.mediaCustom("max-width: 767px") {
    display: none;
  }

  svg {
    height: 13px;
    width: 13px;
    transform: rotate(180deg);
  }
}

.collection-filters__remove {
  border-color: variables.$color-black;
  min-height: 43px;
  font-size: variables.$font-size--general-up-s;
  line-height: variables.$line-height--general-up-s;
  color: variables.$color-black;
  text-transform: uppercase;
}

/* filtros de rango */
.wrapper-filter {
  background: variables.$color-white;

  @include mixins.mediaCustom("max-width: 768px") {
    padding: 16px 30px 0;
  }
}

.label-check {
  position: relative;
}

.color {
  .label-check[data-disable="true"] {
    > span::before {
      position: absolute;
      top: 6px;
      left: 16px;
      width: 2px;
      height: 69%;
      background: #995555;
      content: " ";
      transform: rotate(45deg);
    }
  }
}

.price-in {
  margin-top: 20px;
  @include mixins.boxFlex;

  .field {
    border: 1px solid variables.$color-background;
    height: 45px;
    padding: 0 10px;
    width: 50%;
    color: variables.$color-background-2;
    @include mixins.boxFlex(space-between);

    input {
      border: unset;
      width: 100%;
      text-align: right;
      color: variables.$color-background-2;
    }
  }

  .field:nth-child(1) {
    float: left;
  }

  .field:nth-child(2) {
    display: inline-flex;
  }

  .separator {
    display: block;
    margin: 0 19px;
  }
}

.field {
  input {
    border: 1px solid variables.$color-background-2;
    margin-left: 12px;
    background: variables.$color-white;
    outline: none;
  }

  input[type="number"]::-webkit-inner-spin-button,
  .field input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }
}

.slider-filter {
  position: relative;
  top: 6px;
  height: 8px;
  background: variables.$color-background;
  border-radius: 5px;

  .pro {
    position: absolute;
    right: 4%;
    left: 7%;
    height: 8px;
    background: variables.$color-principal;
    border-radius: 100px;
  }
}

.range-in {
  position: relative;

  input {
    position: absolute;
    top: -3px;
    width: 100%;
    height: 8px;
    -webkit-appearance: none;
    background: none;
    pointer-events: none;
  }

  input[type="range"]::-webkit-slider-thumb {
    width: 17px;
    height: 17px;
    border: 1px solid variables.$color-principal;
    -webkit-appearance: none;
    background: variables.$color-white;
    border-radius: 50px;
    pointer-events: auto;
    cursor: pointer;
  }
}

.ranges {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #777777;
}
