@use "../abstracts/mixins";
@use "./mainproduct";
@import "./mes-alerts";

.mes-alerts {
  align-items: flex-start;
  padding-left: 0;

  .mes-alerts__open-btn {
    @include mixins.mediaCustom("min-width: 768px") {
      width: 100%;
    }
  }
}

.mes-alerts-modal {
  @extend .description-modal;
}

.collection-filters__controls {
  .mes-alerts {
    padding: 0;
    margin-bottom: 12px;

    @include mixins.mediaCustom("max-width: 767px") {
      margin-bottom: 18px;
    }
  }
}
